.header {
  font-family: "Inter", Arial, Helvetica, sans-serif;
  max-width: 880px;
  min-height: 74px;
  border-bottom: 1px solid #545454;
  border-color: rgb(54 54 54 / 0.7);
  display: flex;
  justify-content: space-between;
}

@media (max-width: 460px) {
  .header {
    min-height: 56px;
  }
}
